@import "cssreset.css";
@import url('https://fonts.googleapis.com/css2?family=East+Sea+Dokdo&family=Noto+Sans+KR:wght@300&display=swap');


@keyframes fadeout {
  from {opacity: 1;}
  to {opacity: 0;}
}

@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@keyframes leftnavbar {
  from { transform: translateX(-100%)}
  to { transform: translateX(0);}
}

body {
  font-family: 'Noto Sans KR', sans-serif;
  background-color: rgba(172, 255, 47, 0.103);
}

.App {
  display: flex;
  height: 100vh;
}

/* start button */
.gotrip {
  display: flex;
  margin: auto;
  border-radius: 15px;
  background-color: white;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.459);
  cursor: pointer;
  font-family: 'East Sea Dokdo', cursive;;
  font-size: 15vmin;
  padding: 10px 25px;
  animation: fadein 2s;
}

/* day button */
.daybuttons {
  display: none;
}

.daybuttons.active {
  display: flex;
  flex-direction: column;
  align-content: space-around;
  margin: auto;
}

.btn {
  font-family: 'East Sea Dokdo', cursive;;
  border-radius: 15px;
  background-color: white;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.459);
  cursor: pointer;
  font-size: 10vmin;
  padding: 15px 30px;
  animation: fadein 2s;
  margin: 20px 0;
}


/* main contents */
.container {
  display: none;
}

.container.active {
  display: block;
  transition: .5s ease-in-out;
}


/* left side bar */
.leftnavi{
  font-family: 'East Sea Dokdo', cursive;;
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 100vh;
  background-color: rgba(136, 182, 11, 0.192);
  overflow: hidden;
  display: block;
  transition: .5s ease-in-out;
}

.leftnavi a{
  height: 20px;
  text-align: center;
  margin: 20px 50px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  overflow: hidden;
  border-bottom: 1px rgba(0, 0, 0, 0.521) solid;
  padding-bottom: 10px;
}


/* top side bar */
.navibutton {
  background-image: url("https://image.flaticon.com/icons/png/512/660/660376.png");
  margin: 5px;
  width: 30px;
  height: 30px;
  background-size: cover;
  top:0px;
  opacity: 0.7;
}

.topNaviContainer {
  display: flex;
  justify-content: space-between;
  z-index: 1000;
  box-shadow: 0 1px 2px 0px rgba(0, 0, 0, 0.212);
}

.topnavi {
  display: flex;
}
.topnavi ul{
  display: flex;
  margin: auto;
}

.topnavi ul li {
  margin: 0 5px;
  border-radius: 5px;
  padding: 5px;
  background-color: rgb(215, 241, 213);
}

.Map>img {
  width: 100vw;
  margin-top: 5px;
}

.gotrip.active {
  display: none;
}