@import url(https://fonts.googleapis.com/css2?family=East+Sea+Dokdo&family=Noto+Sans+KR:wght@300&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
@-webkit-keyframes fadeout {
  from {opacity: 1;}
  to {opacity: 0;}
}


@keyframes fadeout {
  from {opacity: 1;}
  to {opacity: 0;}
}

@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@-webkit-keyframes leftnavbar {
  from { -webkit-transform: translateX(-100%); transform: translateX(-100%)}
  to { -webkit-transform: translateX(0); transform: translateX(0);}
}

@keyframes leftnavbar {
  from { -webkit-transform: translateX(-100%); transform: translateX(-100%)}
  to { -webkit-transform: translateX(0); transform: translateX(0);}
}

body {
  font-family: 'Noto Sans KR', sans-serif;
  background-color: rgba(172, 255, 47, 0.103);
}

.App {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
}

/* start button */
.gotrip {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  border-radius: 15px;
  background-color: white;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.459);
  cursor: pointer;
  font-family: 'East Sea Dokdo', cursive;;
  font-size: 15vmin;
  padding: 10px 25px;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

/* day button */
.daybuttons {
  display: none;
}

.daybuttons.active {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: space-around;
          align-content: space-around;
  margin: auto;
}

.btn {
  font-family: 'East Sea Dokdo', cursive;;
  border-radius: 15px;
  background-color: white;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.459);
  cursor: pointer;
  font-size: 10vmin;
  padding: 15px 30px;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  margin: 20px 0;
}


/* main contents */
.container {
  display: none;
}

.container.active {
  display: block;
  transition: .5s ease-in-out;
}


/* left side bar */
.leftnavi{
  font-family: 'East Sea Dokdo', cursive;;
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 100vh;
  background-color: rgba(136, 182, 11, 0.192);
  overflow: hidden;
  display: block;
  transition: .5s ease-in-out;
}

.leftnavi a{
  height: 20px;
  text-align: center;
  margin: 20px 50px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  overflow: hidden;
  border-bottom: 1px rgba(0, 0, 0, 0.521) solid;
  padding-bottom: 10px;
}


/* top side bar */
.navibutton {
  background-image: url("https://image.flaticon.com/icons/png/512/660/660376.png");
  margin: 5px;
  width: 30px;
  height: 30px;
  background-size: cover;
  top:0px;
  opacity: 0.7;
}

.topNaviContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  z-index: 1000;
  box-shadow: 0 1px 2px 0px rgba(0, 0, 0, 0.212);
}

.topnavi {
  display: -webkit-flex;
  display: flex;
}
.topnavi ul{
  display: -webkit-flex;
  display: flex;
  margin: auto;
}

.topnavi ul li {
  margin: 0 5px;
  border-radius: 5px;
  padding: 5px;
  background-color: rgb(215, 241, 213);
}

.Map>img {
  width: 100vw;
  margin-top: 5px;
}

.gotrip.active {
  display: none;
}
